
'use client';
import { products } from "@/lib/data"
import { PlaceHolderImages, ImagePlaceholder } from "@/lib/placeholder-images"
import React from 'react';
import Image from 'next/image';
import Link from 'next/link';
import { Button } from '@/components/ui/button';
import { Card, CardContent } from '@/components/ui/card';
import { Carousel, CarouselContent, CarouselItem, CarouselNext, CarouselPrevious } from '@/components/ui/carousel';
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";

export type ProductWithImage = (typeof products)[0] & {
    imageUrl?: string;
    imageHint?: string;
};

export default function ShopPage() {
    const productsWithImages: ProductWithImage[] = products.map(product => {
        const image = PlaceHolderImages.find((img: ImagePlaceholder) => img.id === product.imageId)
        return { ...product, imageUrl: image?.imageUrl, imageHint: image?.imageHint }
    })
    
    const featuredCakes = productsWithImages.filter(p => p.category === 'Cakes').slice(0, 3);
    const otherProducts = productsWithImages.filter(p => p.id !== featuredCakes[1]?.id);

    const testimonial = {
        name: "Alexandra",
        age: 19,
        handle: "@sandra_miev",
        quote: "The cake was so delicate and airy!",
        avatarUrl: "https://picsum.photos/seed/alexandra/100/100",
        photoUrl: "https://picsum.photos/seed/testimonial-main/600/800",
        photoHint: "woman model"
    };

    const galleryImages = [
        "cupcake-1", "cake-1", "cake-2", "other-1"
    ].map(id => productsWithImages.find(p => p.imageId === id)).filter(Boolean) as ProductWithImage[];


    return (
        <div className="bg-background">
            {/* Hero Section */}
            <section className="relative container mx-auto px-4 py-16 md:py-24 text-center overflow-hidden">
                <h1 className="text-4xl md:text-5xl font-headline text-foreground mb-2">Try our cakes</h1>
                <p className="text-muted-foreground mb-8">sugar-free, flour-free, and fat substitute-free</p>
                <Button asChild>
                    <Link href="/custom-order">Choose a cake</Link>
                </Button>
                <div className="mt-12 grid grid-cols-5 items-center justify-center gap-4">
                    {featuredCakes.length > 0 && featuredCakes[0].imageUrl && (
                         <div className="relative col-start-1 col-span-2 justify-self-start opacity-50 md:opacity-100">
                             <Image src={featuredCakes[0].imageUrl} alt={featuredCakes[0].name} width={400} height={400} className="object-contain" data-ai-hint={featuredCakes[0].imageHint} />
                         </div>
                    )}
                    {featuredCakes.length > 1 && featuredCakes[1].imageUrl && (
                        <div className="relative col-start-2 col-span-3 md:col-start-auto md:col-span-1">
                             <Image src={featuredCakes[1].imageUrl} alt={featuredCakes[1].name} width={500} height={500} className="object-contain" data-ai-hint={featuredCakes[1].imageHint} />
                        </div>
                    )}
                    {featuredCakes.length > 2 && featuredCakes[2].imageUrl && (
                        <div className="relative col-start-4 col-span-2 justify-self-end opacity-50 md:opacity-100">
                             <Image src={featuredCakes[2].imageUrl} alt={featuredCakes[2].name} width={400} height={400} className="object-contain" data-ai-hint={featuredCakes[2].imageHint} />
                        </div>
                    )}
                </div>
            </section>

            {/* Product Carousel Section */}
            <section className="py-16 md:py-24 bg-background">
                <div className="container mx-auto px-4">
                    <h2 className="text-3xl md:text-4xl font-headline text-center text-foreground mb-12">Choose your treat</h2>
                    <Carousel
                        opts={{
                            align: "start",
                            loop: true,
                        }}
                        className="w-full"
                    >
                        <CarouselContent>
                            {otherProducts.map((product) => (
                                <CarouselItem key={product.id} className="md:basis-1/2 lg:basis-1/3">
                                    <div className="p-1 h-full">
                                        <Card className="flex flex-col h-full overflow-hidden text-center items-center shadow-lg transition-shadow hover:shadow-xl">
                                            <div className="w-full h-64 relative mt-6">
                                                {product.imageUrl && <Image src={product.imageUrl} alt={product.name} fill className="object-contain" data-ai-hint={product.imageHint} />}
                                            </div>
                                            <CardContent className="p-6 flex flex-col items-center flex-grow">
                                                <h3 className="text-xl font-headline mb-2">{product.name}</h3>
                                                <p className="text-muted-foreground text-sm flex-grow">{product.description}</p>
                                                <p className="text-2xl font-bold text-primary my-4">{product.price}</p>
                                                <Button variant="outline">Order Cake</Button>
                                            </CardContent>
                                        </Card>
                                    </div>
                                </CarouselItem>
                            ))}
                        </CarouselContent>
                        <CarouselPrevious className="hidden md:flex" />
                        <CarouselNext className="hidden md:flex" />
                    </Carousel>
                </div>
            </section>

             {/* Testimonial Section */}
            <section className="py-16 md:py-24 bg-background">
                <div className="container mx-auto px-4">
                    <h2 className="text-3xl md:text-4xl font-headline text-center text-foreground mb-12">Find out why friends recommend us</h2>
                    <div className="grid md:grid-cols-2 gap-8 items-center max-w-5xl mx-auto">
                        <div className="flex flex-col lg:flex-row items-center gap-6">
                            <div className="relative w-full max-w-[250px] lg:max-w-[180px] shrink-0">
                                {testimonial.photoUrl && (
                                    <Image src={testimonial.photoUrl} alt={testimonial.name} width={400} height={500} className="rounded-lg object-cover" data-ai-hint={testimonial.photoHint} />
                                )}
                            </div>
                            <div className="text-center lg:text-left">
                                <p className="font-semibold text-lg italic mb-4">"{testimonial.quote}"</p>
                                <div className="flex items-center justify-center lg:justify-start gap-3">
                                    <Avatar>
                                        <AvatarImage src={testimonial.avatarUrl} alt={testimonial.name} />
                                        <AvatarFallback>{testimonial.name.charAt(0)}</AvatarFallback>
                                    </Avatar>
                                    <div>
                                        <p className="font-bold">{testimonial.name}, {testimonial.age}</p>
                                        <p className="text-sm text-muted-foreground">{testimonial.handle}</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="grid grid-cols-2 gap-4">
                            {galleryImages.map((img) => (
                                <div key={img.id} className="relative aspect-square">
                                    {img.imageUrl && (
                                        <Image src={img.imageUrl} alt={img.name} fill className="rounded-lg object-cover" data-ai-hint={img.imageHint} />
                                    )}
                                </div>
                            ))}
                        </div>
                    </div>
                </div>
            </section>
        </div>
    )
}
